import { useNavigate } from "react-router-dom";
import BorderBottom from "../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import style from "../MembersTab/staff/Mainbody/StaffAdmisionAdmin/Refund/AdmisisonRefund.module.css";
import { useTranslation } from "react-i18next";
import FormToggle from "../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import { useEffect, useState } from "react";
import { Notfication } from "../validation/Snackbar";
import {
  useAdmissionDashboardMaster,
  useAdmissionTabController,
} from "../optimized/Hooks/ApiHooks/AdmissionManager/admission-manager-api";
const AdmissionTabController = ({ admissionId }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [state, setState] = useState({
    admission_enquiry: true,
    admission_mods: true,
    all_students: true,
    complete_admission: true,
    data_export: true,
    excess_fee: true,
    offline_payment_verification: true,
    ongoing_admission: true,
    pending_fee: true,
    required_documents: true,
  });
  const [run, setRun] = useState(false);
  const [msg, setMsg] = useState("");
  const [admissionTabController] = useAdmissionTabController();
  const { admissionDashboardMaster } = useAdmissionDashboardMaster({
    data: {
      aid: admissionId,
      mid: "",
    },
    skip: !admissionId,
  });

  useEffect(() => {
    if (admissionDashboardMaster?.tab_manage) {
      setState({
        admission_enquiry:
          admissionDashboardMaster?.tab_manage?.admission_enquiry,
        admission_mods: admissionDashboardMaster?.tab_manage?.admission_mods,
        all_students: admissionDashboardMaster?.tab_manage?.all_students,
        complete_admission:
          admissionDashboardMaster?.tab_manage?.complete_admission,
        data_export: admissionDashboardMaster?.tab_manage?.data_export,
        excess_fee: admissionDashboardMaster?.tab_manage?.excess_fee,
        offline_payment_verification:
          admissionDashboardMaster?.tab_manage?.offline_payment_verification,
        ongoing_admission:
          admissionDashboardMaster?.tab_manage?.ongoing_admission,
        pending_fee: admissionDashboardMaster?.tab_manage?.pending_fee,
        required_documents:
          admissionDashboardMaster?.tab_manage?.required_documents,
      });
    }
  }, [admissionDashboardMaster?.tab_manage]);
  const onBackWithArrowLeft = () => {
    navigate(-1);
  };

  const onSubmitFormHandler = (field, value) => {
    const updatedObj = { ...state };
    for (let obj in updatedObj) {
      if (obj === field) {
        updatedObj[obj] = value;
      }
    }
    admissionTabController({
      aid: admissionId,
      tabManage: { tab_manage: updatedObj },
    })
      .then(() => {
        setMsg("update tab access");
        setRun(true);
      })
      .catch({});
    setState((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  return (
    <>
      <div
        className={style.refund_container}
        style={{
          width: "100%",
          minHeight: "50vh",
          backgroundColor: "#ffffff",
          border: "0.037vw solid #cacaca",
          borderRadius: "0.439vw",
          marginTop: "0.3rem",
          padding: "1rem",
        }}
      >
        <section className={style.refund_container_title}>
          <img
            src="/images/arrow-left-fees-icon.svg"
            onClick={onBackWithArrowLeft}
            alt="go to previous tab"
            title="Go Back"
            style={{
              cursor: "pointer",
            }}
          />
          <div className={style.refund_container_title}>
            <h6>{t("tab_controller")}</h6>
          </div>
        </section>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("all_students")}
            checkedToggle={state.all_students}
            onToggleHandler={(e) =>
              onSubmitFormHandler("all_students", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("admission_enquiry")}
            checkedToggle={state.admission_enquiry}
            onToggleHandler={(e) =>
              onSubmitFormHandler("admission_enquiry", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("admission_mods")}
            checkedToggle={state.admission_mods}
            onToggleHandler={(e) =>
              onSubmitFormHandler("admission_mods", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("complete_admission")}
            checkedToggle={state.complete_admission}
            onToggleHandler={(e) =>
              onSubmitFormHandler("complete_admission", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("data_export")}
            checkedToggle={state.data_export}
            onToggleHandler={(e) =>
              onSubmitFormHandler("data_export", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />

          <FormToggle
            labelHeading={t("excess_fee")}
            checkedToggle={state.excess_fee}
            onToggleHandler={(e) =>
              onSubmitFormHandler("excess_fee", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />

        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("ongoing_admission")}
            checkedToggle={state.ongoing_admission}
            onToggleHandler={(e) =>
              onSubmitFormHandler("ongoing_admission", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("pending_fee")}
            checkedToggle={state.pending_fee}
            onToggleHandler={(e) =>
              onSubmitFormHandler("pending_fee", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("required_documents")}
            checkedToggle={state.required_documents}
            onToggleHandler={(e) =>
              onSubmitFormHandler("required_documents", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />

        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("offline_payment_verification")}
            checkedToggle={state.offline_payment_verification}
            onToggleHandler={(e) =>
              onSubmitFormHandler(
                "offline_payment_verification",
                e.target.checked
              )
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
      </div>
      <Notfication
        run={run}
        setRun={setRun}
        msg={msg}
        postiton="bottom"
        type="success"
      />
    </>
  );
};

export default AdmissionTabController;
